import RoomPrivateUserSelect from './Rooms/RoomPrivateUserSelect.vue'
import { createNamespacedHelpers } from 'vuex'
const { mapGetters, mapMutations, mapActions } = createNamespacedHelpers('Chat')
export default {
  name: 'ChatPanelRight',
  components: { RoomPrivateUserSelect },
  props: {
    participants: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      filterParticipants: '',
      userId: CONFIG.userId,
      showAddPanel: false,
      itemActionsIconSize: 'xs'
    }
  },
  computed: {
    ...mapGetters(['currentRoomData', 'config', 'layout']),
    isUserModerator() {
      return (
        this.currentRoomData.roomType === 'private' &&
        (this.currentRoomData.creatorid === this.userId || this.config.isAdmin)
      )
    },
    participantsList() {
      if (this.filterParticipants === '') {
        return this.participants.length
          ? this.participants.concat().sort(this.sortByCurrentUserMessagesName)
          : []
      } else {
        return this.participants.filter(participant => {
          return (
            participant.user_name
              .toLowerCase()
              .includes(this.filterParticipants.toLowerCase()) ||
            participant.role_name
              .toLowerCase()
              .includes(this.filterParticipants.toLowerCase())
          )
        })
      }
    }
  },
  methods: {
    ...mapActions(['removeUserFromRoom']),
    sortByCurrentUserMessagesName(a, b) {
      if (a.user_id === this.userId) {
        return -1
      }
      if (!a.message && b.message) {
        return 1
      } else if (a.message && !b.message) {
        return -1
      }
      if (a.user_name > b.user_name) {
        return 1
      } else {
        return -1
      }
    },
    stripHtml(html) {
      return app.stripHtml(html)
    }
  }
}
