import NoResults from 'components/NoResults.vue'
import { createNamespacedHelpers } from 'vuex'
const { mapGetters, mapActions, mapMutations } = createNamespacedHelpers('Chat')

export default {
  name: 'TabMessages',
  components: { NoResults },
  props: {
    fetchingEarlier: {
      type: Boolean,
      default: false
    },
    header: {
      type: Function
    },
    messageOnClick: {
      type: Function
    },
    roomData: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      userId: CONFIG.userId
    }
  },
  computed: {
    areEntries() {
      return this.roomData.chatEntries === undefined
        ? true
        : this.roomData.chatEntries.length
    }
  }
}
