import { createNamespacedHelpers } from 'vuex'
const { mapActions } = createNamespacedHelpers('Chat')
export default {
  name: 'RoomUnpinButton',
  props: {
    roomType: {
      type: String,
      required: true
    },
    recordId: {
      type: Number,
      required: true
    }
  },
  methods: {
    ...mapActions(['unpinRoom'])
  }
}
