import RoomSelect from './RoomSelect.vue'
import RoomList from './RoomList.vue'
import { createNamespacedHelpers } from 'vuex'
const { mapGetters, mapActions } = createNamespacedHelpers('Chat')
export default {
  name: 'RoomCrm',
  components: { RoomSelect, RoomList },
  props: {
    roomData: {
      type: Array,
      required: true
    },
    roomType: {
      type: String,
      required: true
    },
    filterRooms: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      showAddRoomPanel: false,
      isSelectVisible: false,
      modulesList: []
    }
  },
  computed: {
    ...mapGetters(['config'])
  },
  created() {
    this.modulesList = this.config.chatModules
  },
  methods: {
    updateRoomListSelect(val) {
      this.$refs.roomList.toggleRoomSelect()
    },
    ...mapActions(['fetchRoomList']),
    showRecordsModal(val) {
      app.showRecordsList(
        { module: val, src_module: val },
        (modal, instance) => {
          instance.setSelectEvent((responseData, e) => {
            AppConnector.request({
              module: 'Chat',
              action: 'Room',
              mode: 'addToFavorites',
              roomType: 'crm',
              recordId: responseData.id
            }).done(({ result }) => {
              this.fetchRoomList()
            })
          })
        }
      )
    },
    filter(val, update) {
      if (val === '') {
        update(() => {
          this.modulesList = this.config.chatModules
        })
        return
      }
      update(() => {
        const needle = val.toLowerCase()
        this.modulesList = this.config.chatModules.filter(
          v => v.label.toLowerCase().indexOf(needle) > -1
        )
      })
    },
    onToggleSelectRoom(val) {
      this.isSelectVisible = val
    }
  }
}
